import React, { useState } from 'react';
import './styles/Home.css';
import './styles/Mobile.css';
import { Link } from 'react-router-dom';

function Register() {
    const [pseudo, setPseudo] = useState('');
    const [password, setPassword] = useState('');
    const [confirmPassword, setConfirmPassword] = useState('');
    const [feelings, setFeelings] = useState(["", "", "", ""]); // 4 motions par dfaut
    const [acceptedTerms, setAcceptedTerms] = useState(false);
    const [errorMessage, setErrorMessage] = useState('');

    // const handleSubmit = async (e) => {
    //     e.preventDefault();

    //     if (!acceptedTerms) {
    //         alert("Vous devez accepter les conditions gnrales pour continuer.");
    //         return;
    //     }

    //     if (password !== confirmPassword) {
    //         setErrorMessage("Les mots de passe ne correspondent pas.");
    //         return;
    //     }

    //     setErrorMessage(""); // Rinitialise le message d'erreur

    //     const userData = {
    //         pseudo,
    //         password,
    //         feelings,
    //     };

    //     console.log('User Data:', userData);
    //     // Envoi des donnes  l'API ou base de donnes

    //     try {
    //         const response = await fetch("http://localhost:4000/Register", {
    //             method: "POST",
    //             headers: { "Content-Type": "application/json" },
    //             body: JSON.stringify(userData),
    //         });
    
    //         const data = await response.json();
    //         console.log("Rponse du serveur :", data);
    //     } catch (error) {
    //         console.error("Erreur lors de l'envoi :", error);
    //     }

    //     fetch('/Register', {
    //         method: "POST",
    //         headers: { "Content-Type": "application/json" },
    //         // body: JSON.stringify({ pseudo: document.getElementById("pseudo").value })
    //         body: JSON.stringify(userData)
    //     })
    //     .then(response => response.json())
    //     .then(data => {
    //         console.log("Rponse serveur :", data);
    //         if (data.pseudo) {
    //             window.location.href = "/Login"; //  La redirection se fait ici
    //         }
    //     })
    //     .catch(error => console.error("Erreur :", error));
    // };

    const handleSubmit = async (e) => {
        e.preventDefault();
    
        if (!acceptedTerms) {
            alert("Vous devez accepter les conditions gnrales pour continuer.");
            return;
        }
    
        if (password !== confirmPassword) {
            setErrorMessage("Les mots de passe ne correspondent pas.");
            return;
        }
    
        setErrorMessage(""); // Rinitialise le message d'erreur
    
        const userData = {
            pseudo,
            password,
            feelings,
        };
    
        console.log('User Data:', userData);
    
        try {
            // const API_URL=process.env.REACT_APP_API_URL || "http://localhost:4000";

            // const API_URL="https://myday-back.onrender.com";
            // const API_URL = "http://localhost:4000";

            const API_URL =
                window.location.hostname === "localhost"
                    ? "http://localhost:4000"
                    : "https://myday-back.onrender.com";


            const response = await fetch(`${API_URL}/Register`, {
            // const response = await fetch("http://localhost:4000/Register", {
                method: "POST",
                headers: { "Content-Type": "application/json" },
                body: JSON.stringify(userData),
                credentials: 'include'
            });
    
            const data = await response.json();
            console.log("Rponse du serveur :", data);
    
            if (data) {
                localStorage.setItem('userFeelings', JSON.stringify(feelings));
                window.location.href = "/Login"; //  La redirection se fait ici
            }
        } catch (error) {
            console.error("Erreur lors de l'envoi :", error);
        }
    };
    

    const handleFeelingChange = (index, value) => {
        const updatedFeelings = [...feelings];
        updatedFeelings[index] = value;
        setFeelings(updatedFeelings);
    };

    return (
        <div className="container">
            <div className="header">
                <h1>
                    <span>M</span><span>y</span><span>D</span><span>a</span><span>y</span>
                </h1>
            </div>
            <form onSubmit={handleSubmit}>

                <h5>Tes motions  suivre</h5>
                {feelings.map((feeling, index) => (
                    <h4 key={index}>
                        <label htmlFor={`feeling_${index + 1}`}>motion #{index + 1} : </label>
                        <input
                            className="login-input"
                            type="text"
                            id={`feeling_${index + 1}`}
                            value={feeling}
                            onChange={(e) => handleFeelingChange(index, e.target.value)}
                            required
                        />
                    </h4>
                ))}
                <Link to="/Emotions">
                        <button type="button" className="submit-button-roue">
                            Roue des motions
                        </button>
                </Link>

                <h5>Donnes personnelles</h5>
                <h4>
                    <label htmlFor="pseudo">Pseudo : </label>
                    <input
                        className="login-input"
                        type="text"
                        id="pseudo"
                        value={pseudo}
                        onChange={(e) => setPseudo(e.target.value)}
                        required
                    />
                </h4>
                <h4>
                    <label htmlFor="password">Mot de passe : </label>
                    <input
                        className="login-input"
                        type="password"
                        id="password"
                        value={password}
                        onChange={(e) => setPassword(e.target.value)}
                        required
                    />
                </h4>
                <h4>
                    <label htmlFor="confirmPassword">Confirmez le mot de passe : </label>
                    <input
                        className="login-input"
                        type="password"
                        id="confirmPassword"
                        value={confirmPassword}
                        onChange={(e) => setConfirmPassword(e.target.value)}
                        required
                    />
                </h4>
                {errorMessage && <p className="error-message">{errorMessage}</p>}

                <hr className="hr" />

                <div className='h4-bis'>
                    <input
                        type="checkbox"
                        id="terms"
                        checked={acceptedTerms}
                        onChange={(e) => setAcceptedTerms(e.target.checked)}
                    />
                    <label htmlFor="terms"> J'accepte les conditions gnrales</label>
                </div>
                <div className="boutton-clear-submit-index">
                    <button className="submit-button" type="submit" disabled={!acceptedTerms}>
                        Valider
                    </button>
                    <Link to="/Login">
                        <button type="button" className="submit-button">
                            J'ai dja un compte
                        </button>
                    </Link>
                </div>
                    
            </form>
            <p className ="droits"> 2025 myDay. Tous droits rservs.
        Cette application et son contenu sont protgs par les lois en vigueur sur la proprit intellectuelle. </p>
        </div>
    );
}

export default Register;
